<script setup>
import { onMounted, inject, reactive, ref, watch } from 'vue'
import 'vue3-circle-progress/dist/circle-progress.css'
import CircleProgress from 'vue3-circle-progress'

const axios = inject('axios')

let data = reactive([])
const selectedCounty = ref('F-D0047-005')
const county = reactive([{
  name: '',
  code: 'F-D0047-061'
}, {
  name: '',
  code: 'F-D0047-069'
}, {
  name: '',
  code: 'F-D0047-005'
}])
const allDistricts = reactive({
  :
  ['', '', '', '', '', '', '', '', '', '', '', ''],
  :
  ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''],
  :
  ['', '', '', '', '', '', '', '', '', '', '', '', '']
})
const districts = reactive(['', '', '', '', '', '', '', '', '', '', '', '', ''])
const selectedDistrict = ref('')
const newWeather = reactive([])
const newPop6h = reactive([])

watch(
  () => selectedCounty.value,
  (code) => {
    // 
    districts.length = 0
    const index = county.findIndex(v => v.code === code)
    // county[index].name -> 
    allDistricts[county[index].name].forEach(v => districts.push(v))
  }
)
watch(
  () => selectedDistrict.value,
  () => {
    getData()
  }
)

const getData = () => {
  axios.get(`https://opendata.cwb.gov.tw/api/v1/rest/datastore/${selectedCounty.value}?Authorization=CWB-9509B8D9-DFD6-4AA0-B576-3617F0F4A4F5`)
    .then((res) => {
      data = res.data.records.locations[0].location
      // selected  selectedDistrict 
      const selected = data.find(v => v.locationName === selectedDistrict.value).weatherElement

      // selected3
      const weather = selected.find(v => v.description === '').time
      const temperature = selected.find(v => v.description === '').time
      const pop6h = selected.find(v => v.description === '6').time

      // 32newWeather, newPop6h
      newWeather.length = 0
      weather.forEach(v => {
        newWeather.push({
          : v.startTime.slice(8, 13).replace(' ', ' ').replace('-', '/') + '',
          : v.elementValue[0].value
        })
      })
      temperature.forEach((v, i) => {
        newWeather[i][''] = v.elementValue[0].value + 'C'
      })
      console.log(newWeather)

      newPop6h.length = 0
      pop6h.forEach(v => {
        newPop6h.push({
          : v.startTime.slice(8, 13).replace(' ', ' ').replace('-', '/') + '',
          : v.elementValue[0].value
        })
      })
      console.log(newPop6h)
    })
}
onMounted(() => {
  getData()
})
</script>

<template>
<div class="container-fluid row">
  <div class="col-md-3">
    <NavBar></NavBar>
  </div>
  <div class="col-12 col-md-9 weather_prediction">
    <section class="choose_area container d-flex mt-5">
      <select v-model="selectedCounty" class="form-select mx-2">
        <option value="" disabled></option>
        <option v-for="(v, i) in county" :key="`${i}`"
                :value="v.code">{{ v.name }}</option>
      </select>
      <select v-model="selectedDistrict" class="form-select mx-2">
        <option value="" disabled></option>
        <option v-for="(v, i) in districts" :key="`${i}`"
                :value="v">{{ v }}</option>
      </select>
    </section>
    <section class="container my-5">
      <h2 class="title text-secondary"></h2>
      <div class="container">
        <ul class="d-flex weather_list p-0">
          <li v-for="(v, i) in newWeather" :key="`array${i}`" class="weather_item p-2">
            <div class="h3 text-center text-dark">{{ v[''] }}</div>
            <div class="text-center">{{ v[''] }}</div>
            <div class="text-center">{{ v[''] }}</div>
          </li>
        </ul>
      </div>
    </section>
    <section class="container">
      <h2 class="title text-secondary"></h2>
      <div class="container">
        <ul class="d-flex pop6h_list p-0">
          <li v-for="(v, i) in newPop6h" :key="`array${i}`" class="pop6h_item p-2">
            <circle-progress
              class="h4"
              :size="100"
              :border-width="5"
              :border-bg-width="5"
              :is-gradient="true"
              :gradient="{
                angle: 90,
                startColor: 'skyblue',
                stopColor: 'blue'
              }"
              :percent="Number(v[''])"
              :show-percent="true"
            />
            <div class="text-center">{{ v[''] }}</div>
          </li>
        </ul>
      </div>
    </section>
  </div>
</div>
</template>

<style scoped lang="scss">
li {
  list-style-type: none;
}
h2 {
  font-weight: bold;
}
.weather_list {
  overflow: auto;
  box-shadow: 0 2px 10px lightgray;
  .weather_item {
    flex-shrink: 0;
  }
}
.pop6h_list {
  overflow: auto;
  box-shadow: 0 2px 10px lightgray;
  .pop6h_item {
    flex-shrink: 0;
  }
}
.rain_circle_progress {
  display: block;
  font-size: 2rem;
  font-weight: bold;
}
</style>
